html {
  overflow-x: hidden;
}
body {
  overflow-x: hidden;
  width: 100%;
}

* {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.n-i {
  user-select: none;
  -moz-user-select: none;
  -webkit-user-select: none;
}

#the-parent {
  display: grid;
  grid-template-rows: auto 1fr auto;
  min-height: 100vh;
}

header {
  width: 100%;
  box-sizing: border-box;
  background-color: #393e46;
  padding: 15px 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

header h1 {
  color: #929AAB;
  font-size: 3rem;
}

header input {
  padding: 10px 7px;
  width: 200px;
  border-radius: 100px;
  outline: none;
  border: #929AAB 2px solid;
  background-color: rgba(255, 255, 255, 0.75);
  color: black;
}
header input::placeholder {
  color: gray;
  font-size: 1em;

}

footer {
  width: 100%;
  box-sizing: border-box;
  background-color: #393e46;
  padding: 15px 10px;
  color: #ebebeb;
  display: flex;
  flex-direction: column;
  gap: 20px;
  text-align: center;
}
footer ul {
  list-style: none;
}
footer a {
  color: #ebebeb;
  font-style: italic;
}

main {
  padding: 15px 5px;
  justify-items: center;
}

/* - STYLING - */
.trinn {
  width: 470px;
  padding: 5px 7px 10px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-bottom: #393e46 solid 2px;
}
.trinn:hover {
  cursor: pointer;
  outline: 1px rgba(128, 128, 128, 0.3) solid;
}
.trinn:active {
  cursor: unset;
  outline: 1px rgba(128, 128, 128, 0.3) solid;
  background-color: rgba(128, 128, 128, 0.1);
}

.trinn img {
  aspect-ratio: 1;
  height: 1.2em;
}

/* - MEDIA QUERIES - */
@media (max-width: 676px) {
  .trinn {
    width: 300px;
  }
  header {
    flex-direction: column;
    justify-content: unset;
    gap: 15px;
  }
}